const ProductItem = ProductItem_Cmpt();

const TabsHRM = TabsHRM_Cmpt();

const ProductDashboard = ProductDashboard_Cmpt();
const MovementesDashboard = MovementesDashboard_Cmpt();



const InventoryDashboard  = ({ data, elmId, updObs }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	const [view, set_view] = useState(1);
	const [loading, set_loading] = useState(false);




	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			set_obs(gen16CodeId())
			get_balances_by_type("global_accounts","global_account_params",updObs);
		}
	});


	const openUpdateView =  () => {
		
	}


	



	

	const cb_result = async (f, v) => {
		set_obs(gen16CodeId())
		set_loading(false);
	};



	return (

	<div className="">
			<TabsHRM  
				indexTab={view} 
				data={[
					{key:1,label:"Movements"}, 
					{key:2,label:"Products"}
				]} 
				updateIndex={set_view}  
			/>
				


				<OpacityView viewId={1} activeView={view}>
					<MovementesDashboard />
					</OpacityView>


				<OpacityView viewId={2} activeView={view}>
					<ProductDashboard />
				</OpacityView>
				
			
	</div>
	);
};

export default InventoryDashboard;



const OpacityView  = ({ viewId, activeView, children }) => {
	return(
		<div className={"opacityView"} style={{opacity : viewId=== activeView?1:0 }}>
			{viewId=== activeView?children:null}
		</div>
	)
}
